import Glide from '@glidejs/glide'
import AOS from 'aos';

if (document.querySelector("#home-slider")) {
  new Glide("#home-slider", {
    type: "carousel",
    startAt: 0,
    perView: 1,
    gap: 0,
    autoplay: 3500,
    animationDuration: 1500
  }).mount();
}

if (document.querySelector("#feedback-slider")) {
  new Glide("#feedback-slider", {
    type: "carousel",
    startAt: 0,
    perView: 1,
    gap: 2,
    autoplay: 3500,
    animationDuration: 1500,
    peek: {
      before: 60,
      after: 60
    }
  }).mount();
}

AOS.init({
	once: true,
	duration: 1000
});
